import { useSelector } from 'react-redux';

import { checkPermission } from '@/utils/permission';
import DeniedPage from '@/components/denied.component';

export function PermissionRoute({ permissions, element }) {
  const { user } = useSelector((state) => state.user);
  const checkPermissionResult = checkPermission(permissions, user.permissions);
  return checkPermissionResult ? element : <DeniedPage />;
}
