import { Layout, Spin, Drawer } from 'antd';
import { ErrorBoundary } from 'react-error-boundary';

import { MenuUnfoldOutlined, MenuFoldOutlined } from '@ant-design/icons';
import React, { Suspense, useEffect, useState } from 'react';
import { Outlet } from 'react-router-dom';
import { useMediaQuery } from '@koj-react/hooks';
import { css } from '@emotion/css';
import { headerHeight } from '@/constants/default-value';
import SidebarMenu from './sidebar.component';
import NotFoundPage from './not-found.component';
import DeniedPage from './denied.component';
import { NETWORK_ERROR } from '@/constants/error-code.';
import NetworkErrorPage from './network-error.component';

const { Header, Content, Sider } = Layout;

function ErrorFallback({ error, resetErrorBoundary }) {
  if (error.errorCode === NETWORK_ERROR) {
    return <NetworkErrorPage />;
  }

  return <NotFoundPage />;
}

function LayoutDashboard(props) {
  const [collapsed, setCollapsed] = useState(false);
  const isNotMobile = useMediaQuery('(min-width: 992px)');

  // let isMobile = false;
  const onCollapse = () => {
    setCollapsed((collapsed) => !collapsed);
  };
  useEffect(() => {
    console.log('component mount');
    return () => {
      console.log('component unmount');
    };
  }, []);

  return (
    <Layout className={layoutDashboardStyle}>
      {isNotMobile ? (
        <Header className={header}>
          <div
            className="logo"
            style={{ width: `${collapsed ? 48 : 200}px` }}
          />
          {React.createElement(
            collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
            {
              className: 'trigger collapse',
              onClick: onCollapse,
            }
          )}
        </Header>
      ) : (
        <Header className={header}>
          <div className="logo" style={{ width: 48 }} />
          {React.createElement(
            collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
            {
              className: 'trigger collapse',
              onClick: onCollapse,
            }
          )}
        </Header>
      )}
      <Layout className="child-layout">
        {isNotMobile ? (
          <Sider
            // trigger={null}
            collapsible
            collapsed={collapsed}
            onCollapse={onCollapse}
            collapsedWidth={48}
          >
            <SidebarMenu />
          </Sider>
        ) : (
          <Drawer
            title="Basic Drawer"
            onClose={onCollapse}
            closable={false}
            visible={collapsed}
            placement="left"
            className={`${drawerStyle} extend-sider-bg`}
          >
            <SidebarMenu />
          </Drawer>
        )}
        <Content className="content">
          <Suspense
            fallback={
              <div className="center-screen">
                <Spin size="large" />
              </div>
            }
          >
            <ErrorBoundary
              FallbackComponent={ErrorFallback}
              onReset={() => {
                // reset the state of your app so the error doesn't happen again
              }}
            >
              <Outlet />
            </ErrorBoundary>
          </Suspense>
        </Content>
      </Layout>
    </Layout>
  );
}

const layoutDashboardStyle = css`
  height: 100vh;
  overflow: hidden;
  & header {
    height: ${headerHeight};
    position: sticky;
    top: 0;
    z-index: 1;
  }
  & .child-layout {
    height: calc(100vh - ${headerHeight})
  }
  & + ant-layout-sider {    
      height: calc(100vh - ${headerHeight} - 48px);
      top: ${headerHeight} 
      position: sticky;
    
  }
  & .content {
    height: calc(100vh - ${headerHeight});
    overflow: auto;
    padding: 20px 20px 0;
  }
`;

const header = css`
  padding: 0;
  display: flex;
  align-items: center;
  & .logo {
    height: 100%;
    background-color: #fff;
    transition: all 0.2s;
  }
  & .collapse {
    color: #fff;
    padding: 0 20px;
    font-size: 18px;
    cursor: pointer;
  }
`;

const drawerStyle = css`
  & .ant-drawer-body {
    padding: 0;
    height: 100%;
  }
  & .ant-drawer-header {
    height: ${headerHeight};
  }

  & .extend-sider-bg {
  }
`;
export default LayoutDashboard;
