import { Button, Result } from 'antd';
import { useNavigate } from 'react-router-dom';

const DeniedPage = () => {
  const navigate = useNavigate();

  return (
    <Result
      status="403"
      title="Denied"
      subTitle="Sorry you don't have permission"
      extra={
        <Button type="primary" onClick={() => navigate('/')}>
          Go to Home Page
        </Button>
      }
    ></Result>
  );
};

export default DeniedPage;
